import React, { useState, useEffect } from "react";
import admin from "../img/admin.svg";
import {
  collection,
  getDocs,
  addDoc,
  doc,
  deleteDoc,
  setDoc,
  updateDoc 
} from "firebase/firestore";
import swal from "sweetalert";
import { db } from "../firebaseconfig";
import { async } from "@firebase/util";

function Tareas() {
  const [tarea, setTarea] = useState("");
  const [lista, setLista] = useState([]);
  const [spinner, setSpinner] = useState(false);
  const [editar, setEditar] = useState(false);
  const [id, setId] = useState("");

  const Agregar = async (e) => {
    e.preventDefault();
    setSpinner(true);
    if (tarea !== "") {
      if (!editar) {
        try {
          const docRef = await addDoc(collection(db, "task"), {
            tarea: tarea,
          });
        } catch (e) {
          swal("No se pudo agregar la tarea", e, "error");
        }
      } else {
        const item = doc(db, "task",id);
        await updateDoc(item, {
          tarea: tarea
        });
      }
      Lista();
    } else
      swal(
        "No se pudo completar la tarea",
        "Por favor complete el campo",
        "error"
      );
    setTarea("");
    setSpinner(false);
    setEditar(false);
  };

  const Lista = async () => {
    setSpinner(true);
    const querySnapshot = await getDocs(collection(db, "task"));
    setLista(querySnapshot.docs);
    setSpinner(false);
  };

  const Borrar = async (id) => {
    setSpinner(true);
    try {
      await deleteDoc(doc(db, "task", id));
      Lista();
      setSpinner(false);
    } catch (e) {
      swal("No se pudo eliminar la tarea", e, "error");
      setSpinner(false);
    }
  };

  const Editar = (item) => {
    setEditar(true);
    setTarea(item.data().tarea);
    setId(item.id);
  };

  useEffect(() => {
    Lista();
  }, []);

  return (
    <div className="container">
      <div className="row">
        <div className="col-lg-6">
          <div className="admin">
            <h1>TAREAS</h1>
          </div>
          <div className="text-center">
            <h2>Agregar Tarea</h2>
            <div className="container">
              <form onSubmit={(e) => Agregar(e)} className="form-group">
                <div className="input-group mt-3 mb-4">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Ingrese su tarea..."
                    onChange={(e) => setTarea(e.target.value)}
                    value={tarea}
                  />
                  <div className="input-group-append">
                    <button className="btn btn-secondary" type="submit">
                      {editar ? "Modificar" : "Agregar"}
                    </button>
                  </div>
                </div>
              </form>
              {spinner ? (
                <div className="lds-circle">
                  <div></div>
                </div>
              ) : (
                <span></span>
              )}
            </div>
            <hr />
            <h2>Listado de Tareas</h2>
            <div className="container">
              <ul className="list-group mb-5">
                {lista.map((item) => {
                  return (
                    <li
                      key={item.id}
                      className="list-group-item d-flex justify-content-between align-items-center"
                    >
                      {item.data().tarea}
                      <div>
                        <i
                          className="bi bi-trash-fill text-danger mr-2"
                          onClick={(id) => Borrar(item.id)}
                        ></i>
                        <i
                          className="bi bi-pencil-square text-info"
                          onClick={(tarea) => Editar(item)}
                        ></i>
                      </div>
                    </li>
                  );
                })}
              </ul>
            </div>
          </div>
        </div>
        <div className="col-lg-6" style={{ marginTop: "7rem", zIndex: "-10" }}>
          <img className="img-fluid" src={admin} alt="" />
        </div>
      </div>
    </div>
  );
}

export default Tareas;
